import {PureComponent} from 'react';
import styles from './main.module.css';

export default class Banner extends PureComponent {

  render() {
    return (
        <div>
          <div className={styles.banner}>
            <div className={styles.container}>
              <div className={styles.searchForm} id="search-form">
                <form className={styles.banner__form} id="banner__form" method="POST" action="">
                  <div>
                    <input className={styles.banner__input} id="banner__input" type="text" placeholder="Enter link" autocomplete="off" name='url' maxlength="100" />
                      <button type="submit" className={styles.banner__btn} id={styles.banner__btn}></button>
                  </div>
                </form>

                <h1 id={styles.loadingText}>loading your video...</h1>
              </div>
            </div>
          </div>
        </div>
    );
  };
}